import {ascending, bisector, max as d3max, extent, median} from 'd3-array'
import {axisBottom, axisLeft, axisRight} from 'd3-axis'
import {pointer, select} from 'd3-selection'
import {scaleLinear, scaleUtc} from 'd3-scale'
import type {Tip} from '../d3/tip'
import {line as d3line} from 'd3-shape'
import {format} from 'd3-format'
import formatSymbol from '../d3/format-symbol'
import {utcFormat} from 'd3-time-format'

interface Dimensions {
  width: number
  height: number
  margin: {
    top: number
    right: number
    bottom: number
    left: number
  }
}

export interface GraphData {
  error: string
  summary: {
    total: number
    unique: number
  }
  counts: Array<{bucket: number; date: Date; total: number; unique: number}>
}

function numberFormat(value: number): string {
  const fn = format(',')
  return fn(value)
}

export function renderTrafficGraph<T>(container: Element, data: GraphData, tip: Tip<T>, dimensions: Dimensions) {
  const margin = dimensions.margin
  const width = dimensions.width - margin.left - margin.right
  const height = dimensions.height - margin.top - margin.bottom
  if (!data || data.error != null) {
    return
  }
  const x = scaleUtc().range([0, width])
  let y = scaleLinear().range([height, 0])
  const y2 = scaleLinear().range([height, 0])
  const shortFormat = utcFormat('%m/%d')
  const xAxis = axisBottom(x)
    .ticks(data.counts.length)
    .tickSize(height + 5)
    .tickPadding(10)
    // @ts-expect-error types should be validated here
    .tickFormat(shortFormat)
  // @ts-expect-error types should be validated here
  let yAxis = axisLeft(y).ticks(3).tickFormat(formatSymbol)
  const line = d3line()
    // @ts-expect-error types should be validated here
    .x(d => x(d.key))
    // @ts-expect-error types should be validated here
    .y(d => y(d.value))
  const vis = select(container)
    .select('.js-graph')
    .append('svg')
    .attr('viewBox', `0 0 ${width + margin.left + margin.right} ${height + margin.top + margin.bottom}`)
    .attr('preserveAspectRatio', 'xMinYMin meet')
    .attr('class', 'viz')
    .attr('data-hpc', '')
    .append('g')
    .attr('transform', `translate(${margin.left},${margin.top})`)
    .call(tip)
  const counts = data.counts
  for (const c of counts) {
    c.date = new Date(c.bucket * 1000)
  }
  counts.sort((a, b) => ascending(a.date, b.date))
  // @ts-expect-error types should be validated here
  const bisectAtDate = bisector(d => d.date).left

  // @ts-expect-error types should be validated here
  function showFocusedValue(event) {
    let elements
    const x0 = x.invert(pointer(event)[0])
    const idx = bisectAtDate(counts, x0, 1)
    const d0 = counts[idx - 1]
    const d1 = counts[idx]
    if (d0 && d1) {
      // @ts-expect-error types should be validated here
      const d = x0 - d0.date > d1.date - x0 ? d1 : d0
      // @ts-expect-error types should be validated here
      elements = vis.selectAll('g.dots circle').filter(z => z.key === d.date)
      elements = elements.nodes()
      // @ts-expect-error types should be validated here
      elements.sort((a, b) => Number(a.getAttribute('cy')) - Number(b.getAttribute('cy')))
      return tip.show({target: elements[0]}, d)
    }
  }

  const totals = counts.map(item => ({key: item.date, value: item.total}))
  const uniques = counts.map(item => ({key: item.date, value: item.unique}))

  const series = [totals, uniques]
  const [startDay, endDay] = extent(counts, c => c.date.getTime())
  const ref1 = extent(totals, t => t.value)
  const vmax = ref1[1]
  const umax = d3max(uniques, u => u.value)
  // @ts-expect-error types should be validated here
  const umax1 = umax + median(uniques, d => d.value)
  // @ts-expect-error types should be validated here
  x.domain([startDay, endDay])
  // @ts-expect-error types should be validated here
  y.domain([0, vmax])
  y2.domain([0, umax1])
  const totalCount = data.summary.total
  const trafficTotalEl = container.querySelector('.js-traffic-total')
  if (trafficTotalEl) trafficTotalEl.textContent = numberFormat(totalCount)
  const uniqueCount = data.summary.unique
  const trafficUniquesEl = container.querySelector('.js-traffic-uniques')
  if (trafficUniquesEl) trafficUniquesEl.textContent = numberFormat(uniqueCount)
  vis.append('g').attr('class', 'x axis').call(xAxis)
  vis.append('g').attr('class', 'y axis views').call(yAxis)
  vis
    .selectAll('path.path')
    .data(series)
    .enter()
    .append('path')
    .attr('class', (d, i) => `path ${i === 0 ? 'total' : 'unique'}`)
    // @ts-expect-error types should be validated here
    .attr('d', (d, i) => (i === 0 ? line(d) : line.y(v => y2(v.value))(d)))
  const dots = vis
    .selectAll('g.dots')
    .data(series)
    .enter()
    .append('g')
    .attr('class', (d, i) => (i === 0 ? 'dots totals' : 'dots uniques'))
  dots.each(function (d, i) {
    /* eslint-disable-next-line @typescript-eslint/no-invalid-this */
    const el = select(this)
    if (i === 1) {
      y = y2
    }
    return (
      el
        .selectAll('circle')
        // @ts-expect-error types should be validated here
        .data(z => z)
        .enter()
        .append('circle')
        // @ts-expect-error types should be validated here
        .attr('cx', z => x(z.key))
        // @ts-expect-error types should be validated here
        .attr('cy', z => y(z.value))
        .attr('r', 4)
    )
  })
  // @ts-expect-error types should be validated here
  yAxis = axisRight(y2).ticks(3).tickFormat(formatSymbol)
  vis.append('g').attr('class', 'y axis unique').attr('transform', `translate(${width}, 0)`).call(yAxis)
  vis
    .append('rect')
    .attr('class', 'overlay')
    .attr('width', width)
    .attr('height', height)
    .on('mousemove', showFocusedValue)
    .on('mouseout', function () {
      setTimeout(tip.hide, 500)
    })
}
