import {controller, target} from '@github/catalyst'
import type {GraphData} from './traffic'
import d3Tip from '../d3/tip'
import {fetchPoll} from '@github-ui/fetch-utils'
import {format} from 'd3-format'
import memoize from '@github/memoize'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
import {renderTrafficGraph} from './traffic'
import {utcFormat} from 'd3-time-format'

interface Data {
  total: number
  unique: number
  date: Date
}

const longFormat = utcFormat('%A, %B %-d, %Y')
const numberFormat = format(',')

// eslint-disable-next-line @typescript-eslint/no-explicit-any
type GraphNetworkData = any

const cachedData = memoize(fetchData)
async function fetchData(url: string): Promise<GraphNetworkData> {
  const response = await fetchPoll(url, {headers: {accept: 'application/json'}})
  return await response.json()
}

@controller
class TrafficClonesGraphElement extends HTMLElement {
  @target graph: HTMLElement
  @target container: HTMLElement

  async connectedCallback() {
    const graph = this.graph
    const url = graph.getAttribute('data-url')
    if (!url) {
      return
    }

    // Remove possibly existing cached graph.
    /* eslint-disable-next-line custom-elements/no-dom-traversal-in-connectedcallback */
    for (const el of graph.querySelectorAll('svg.viz')) {
      el.remove()
    }

    graph.classList.add('is-graph-loading')
    graph.classList.remove('is-graph-load-error', 'is-graph-empty')

    try {
      const data = await cachedData(url)
      graph.classList.remove('is-graph-loading')
      if (data != null && data.unusable) {
        graph.classList.add('is-graph-without-usable-data')
      } else if (
        (data != null && data.length === 0) ||
        (data != null && data.summary != null && data.summary.total === 0) ||
        (data[0] != null && data[0].weeks != null && data[0].weeks.length === 0)
      ) {
        graph.classList.add('is-graph-empty')
      } else {
        render(this.container, data)
      }
    } catch (error) {
      graph.classList.remove('is-graph-loading')
      graph.classList.add('is-graph-load-error')
      throw error
    }
  }
}

function render(container: HTMLElement, data: GraphData) {
  const clonesTip = d3Tip<Data>()
    .attr('class', 'svg-tip web-views comparison')
    .offset([-10, 0])
    .html((d: Data) => {
      const totalWord = d.total === 1 ? 'clone' : 'clones'
      const uniqueWord = d.unique === 1 ? 'unique cloner' : 'unique cloners'
      // eslint-disable-next-line github/unescaped-html-literal
      return `<span class='title'>${longFormat(
        d.date,
      )}</span> <ul class='web-views'> <li class='totals metric-0'><strong>${numberFormat(
        d.total,
      )}</strong> ${totalWord}</li> <li class='uniques metric-1'><strong>${numberFormat(
        d.unique,
      )}</strong> ${uniqueWord}</li> </ul>`
    })
  observe('#js-clones-graph .js-graph', {
    remove() {
      clonesTip.hide()
    },
  })
  const dimensions = {
    width: 719,
    height: 243,
    margin: {
      top: 20,
      right: 40,
      bottom: 30,
      left: 40,
    },
  }
  renderTrafficGraph(container, data, clonesTip, dimensions)
}
